const Noodl = require("@noodl/noodl-sdk");
const { useEffect } = require("react");
const Parser = require("rss-parser");

function RSSFeed({ url, onFeedData = () => { }, children }) {
    const parser = new Parser();
    async function getFeedData(url) {
        return await parser.parseURL(url);
    }

    useEffect(() => {
        getFeedData(url)
            .then((data) => onFeedData(data))
            .catch((error) => console.log(error));
    }, [url]);


    return children;
}

const RSSFeedNode = Noodl.defineReactNode({
    name: "RSS Feed",
    category: "Tutorial",
    getReactComponent() {
        return RSSFeed;
    },
    inputProps: {
        url: { type: "string", default: "https://coinacademy.fr/actu/ethereum?feed=gn" },
    },
    outputProps: {
        onFeedData: { type: "data", displayName: "Feed Items" },
    },
});

module.exports = { RSSFeedNode };
